@import '../Variables/variables.scss';

#contactSectionWrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $main_color_lightMode;
    height: $main_section_height;

    transition: background-color $main_transition_time ease;

    &.dark {
        background: $main_color_darkMode;
        box-shadow: inset 0 8px 40px hsla(0, 0%, 15%, 0.2);
    }


    #contactInfoContainer {
        position: absolute;
        top: 5vh;
        right: 5vw;
        z-index: 1;
        background-color: $main_color_lightMode;
        border-radius: 20px;

        width: 1000px;

        box-shadow: 0 8px 40px rgba(0, 0, 0, 0.5),
            inset 0 4px 4px rgba(255, 255, 255, 0.2),
            inset 0 -4px 4px rgba(255, 255, 255, 0.2);


        .contactInfo {
            //@include button;
            padding: 5px 10px;
            font-size: x-large;
            font-weight: 700;
            display: block;
            border: 1px solid black;
        }

        form {
            grid-area: form;
        }
    }

    .map-container {
        width: 98%;
        height: 98vh;
        margin: auto;
    }
}