#contactSectionWrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #f2f2f2;
  height: 100vh;
  -webkit-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
}

#contactSectionWrapper.dark {
  background: #333333;
  -webkit-box-shadow: inset 0 8px 40px rgba(38, 38, 38, 0.2);
          box-shadow: inset 0 8px 40px rgba(38, 38, 38, 0.2);
}

#contactSectionWrapper #contactInfoContainer {
  position: absolute;
  top: 5vh;
  right: 5vw;
  z-index: 1;
  background-color: #f2f2f2;
  border-radius: 20px;
  width: 1000px;
  -webkit-box-shadow: 0 8px 40px rgba(0, 0, 0, 0.5), inset 0 4px 4px rgba(255, 255, 255, 0.2), inset 0 -4px 4px rgba(255, 255, 255, 0.2);
          box-shadow: 0 8px 40px rgba(0, 0, 0, 0.5), inset 0 4px 4px rgba(255, 255, 255, 0.2), inset 0 -4px 4px rgba(255, 255, 255, 0.2);
}

#contactSectionWrapper #contactInfoContainer .contactInfo {
  padding: 5px 10px;
  font-size: x-large;
  font-weight: 700;
  display: block;
  border: 1px solid black;
}

#contactSectionWrapper #contactInfoContainer form {
  grid-area: form;
}

#contactSectionWrapper .map-container {
  width: 98%;
  height: 98vh;
  margin: auto;
}
